.App {
  text-align: center;
}

html {
  background-color: #282c34;
}

html, body {
  height: auto;
  width: auto;
  min-height: 100%;
  min-width: 100%;
}
.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40vmin;
  pointer-events: none;
}

.sidebar_list {
  width: 100%;
  margin: auto;
}

.sidebar_name_active {
  font-weight: bold
}

.sidebar_name {
  cursor: pointer;
}

.sidebar_name:hover {
  font-weight: bold
}

.lang {
  text-align: right;
  padding-right: 5%;
}


.main_background {
  /* background-image: "/img/main_background.jpg"; */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 400px;
  position: relative;
  z-index: 0;
  overflow: hidden;
}

.main_background:before {
  content: '';
  background: inherit;
  filter: blur(5px);
  position: absolute;
  top: -5px;
  left: -5px;
  right: -5px;
  bottom: -5px;
  z-index: -1;
}

.fa-github {
  color: black;
}

.fa-facebook {
  color: #305097;
}

.fa-twitter {
  color: #00aced;
}

.fa-linkedin {
  color: #0077B5;
}

.main-content{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.profile_list {
  width: 100%;
  display: flex;
}

.profile_list:last-child {
  border-bottom: none;
}

.profile_index {
  padding-top: 45px;
  width: 30%;
  height: 250px;
  font-size: 2em;
}

.profile_content {
  padding-top: 58px;
  padding-left: 10%;
  padding-right: 10%;
  width: 70%;
  min-height: 250px;
}


