@media screen and (min-width: 541px) {
  .side {
    position: sticky;
    width: 15%;
    height: 100vh;
    top: 0;
  }
  .main {
    width: 85%;
    min-height: 100vh;
  }
      
  .sidebar_point {
    display: inline-block;
    position: relative;
    right: 40px;
    cursor: pointer;
    z-index: 3;
  }
  
  .sidebar_point:after {
    content: '';
    position: absolute;
    width: 8px;
    height: 8px;
    left: 0;
    top: 6px;
    right: -1.7px;
    margin: auto;
    border: 6px solid #fff;
    background-color: #fff;
    border-radius: 50%;
    -webkit-transform: scale(.3);
    transform: scale(.3);
    transition: all .25s cubic-bezier(.94,.005,.09,1);
  }
    
  .sidebar_point_active:after {
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
    transition: all .25s cubic-bezier(.94,.005,.09,1);
  }
  
  .lang {
    text-align: right;
    padding-right: 5%;
  }
  
  .main {
    width: 85%;
    min-height: 100vh;
  }
  
  .main_profile {
    margin-top: 50px;
  }
  
  .main_info {
    height: 50%;
    text-align: left;
    position: absolute;
    bottom: 0;
    /* right: 10px; */
    width: 100%;
    padding-left: 10%;
    margin-bottom: 10px;
    font-size: 2em;
  }
  
  .top_profile {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    height: 160px;
  }
  
  .main_name {
    font-size: 1.3em;
    height: 40px;
    padding-left: 20px;
  }
  
  .profile_image {
    width: 100px;
    height: 100px;
  }
  .main_account a {
    font-size: 0.5em;
    padding-right: 5px;
  }
  .profile_list {
    min-height: 250px;
    padding-bottom: 20px;
  }

  .profile_index {
    padding-left: 20%;
  }

}
